@import '../../../../styles/variables/variables';

@mixin default_input {
    max-width: 250px;
    width: 100%;
    outline: none;
    border: 1px solid $borderColor;
    border-radius: 8px;
    padding: 7px 15px;
}

.rigth_block {
    padding: 30px 20px 30px;
    width: 100%;
    flex: 0.5;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-right: 1px solid $borderColor;

    .change_password_block {
        width: 100%;

        .change_password {
            font-size: 20px;
            font-weight: 500;
        }
        .block_old_password {
            display: flex;
            justify-content: space-between;
            max-width: 500px;
            margin-top: 10px;
            .old_password {
                margin-left: 50px;
                font-size: 14px;
                font-weight: 400;
                margin-top: 10px;
            }

        }
        @media (max-width: 1250px) {
            .old_password {
                margin-left: 0 !important;
                margin-bottom: 10px;
            }
        }
        .current_email_block {
            display: flex;

        }

        .code_input_block {
            max-width: 280px;
            width: 100%;
            display: flex;
            justify-content: flex-start;
        }

        .block_curret_email {
            padding: 20px 0 10px;
            justify-content: space-between;
            display: flex;
            align-items: center;
            width: 100%;

          

            p {
                margin-left: 50px;
            }
            input {
                margin-right: 18px;
            }

        }


        @media (max-width: 1250px) {
            .block_curret_email {
                flex-direction: column;
                align-items: flex-start;
            }
        
        }
        @media (max-width: 800px) {
            .block_curret_email {
                padding: 10px 0 5px;
            }
        
        }

    }



    

  

    .default_input {
        @include default_input
    }

    .default_input_block {
        max-width: 370px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        position: relative;

        .current_email {
            cursor:not-allowed;
            color: rgba(0, 0, 0, 0.281);
        }
    }

   

    .change_email_block {
        margin-top: 20px;
    }

    .send {
        cursor: pointer;
        font-weight: 600;
        border: none;
        background-color: transparent;

    }

    .code_input {
        @include default_input;
        max-width: 100px;
        width: 100%;
    }
}

.error_flex {
    display: flex;
    flex-direction: row;
    align-items: center;
}

@media (max-width: 880px) {
    .default_input_block {
        flex-direction: column;
        align-items: flex-start;
    }
    .default_input {
        margin-bottom: 10px;
    }
    .error_flex {
        display: flex;
        flex-direction: row-reverse;
    }

}


